.wrapper__aboutGame {
    width: inherit;
    height: auto;
    font: normal small-caps bold 16px cursive;
}


.wrapper__aboutGame__intro {
    position: relative;
    top: 11%;
    width: 30%;
    margin-top: 10%;
    padding-left: 5%;
    transform: rotate(12deg);
    font-family: Arial, Helvetica, sans-serif;

}
@media (max-width: 400px) {
    .wrapper__aboutGame__intro {
        width: 45%;
        padding-left: 9%;
    }
}

.wrapper__aboutGame__rules {
    margin-top: 19%;
    margin-left: 70%;
    width: 30%;
    transform: rotate(-12deg);
    font-family: Arial, Helvetica, sans-serif;
}
@media (max-width: 400px) {
    .wrapper__aboutGame__rules {
        width: 45%;
        margin-left: 50%;
    }
}

.wrapper__aboutGame__description {
    top: 15%;
    margin-top: 15%;
    background-color: #010e1b;
    width: inherit;
    height: auto;
     background:  linear-gradient(to bottom,  transparent calc(-0% - 1px), #010e1b calc(10% + 50px), #010e1b 100%),
 no-repeat top center;
}

.wrapper__aboutGame__reserv {
    position: absolute;
    min-width: 15%;
    height: 50px;
    border: none;
    font-size: clamp(10px, 15px, 20px);
    background-color: #010e1b;
    color: white;
    margin-top: 14%;
    margin-left: 7%;
    cursor: pointer;
}
.wrapper__aboutGame__reserv:hover {
    animation: shake 0.6s infinite;
}

@keyframes shake {
    0% {
        transform: translate(0px, 0px) rotate(0deg);
    }

    25% {
        transform: translate(1px, 1px) rotate(1deg);
    }

    50% {
        transform: translate(-1px, -1px) rotate(-1deg);
    }

    75% {
        transform: translate(2px, -1px) rotate(2deg);
    }

    100% {
        transform: translate(-1px, 1px) rotate(-1deg);
    }
}